import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {AccountServiceProvider} from "../../providers/account-service/account-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-view-account-information01',
  templateUrl: 'view-account-information01.html',
})
export class viewAccInfo01 {

  accInfoError01;
  accounts = [];
  accountNumber;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public accountService: AccountServiceProvider,
              public gaProvider : GoogleAnalyticsProvider) {

    let dAccount = this.storage.retrieveFromStorage('accounts');
    dAccount = cryptoUtil.getDecryptVal(dAccount);
    this.accounts = JSON.parse(dAccount);
  }

  viewAccountInfoAction = function (account) {
    this.accInfoError01 = "";
    this.accountNumber = account.accountNo;
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenComplete(data);
      });
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encAccNo = this.cryptoUtil.getEncryptVal(this.accountNumber);

      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.accountService.getAccountInformation(unMaskToken, appData.userId, appData.appId, encAccNo)
        .then(data => {
          this.onViewAccInfoFetchComplete(data);
        });
    }
  };

  onViewAccInfoFetchComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('ViewAccountInformation02Page', {'accountDetails': JSON.stringify(data.object)});
    }

    else if (data.statusCode == 0) {
      this.accInfoError01 = data.note;
    }

    else {
      this.accInfoError01 = "System Error Contact with Administrator.";
    }
  };


  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Account Information");
  }

}



